body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  position: relative;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;

  font-family: Roboto;
  opacity: 0.96;
  color: #000000;
}
.App h1 {
  font-size: 60px;
  font-weight: 400;
  line-height: 68px;
}
.App h2 {
  opacity: 0.48;
  color: #ffffff;
  font-weight: 400;
  line-height: 120px;
  text-transform: uppercase;
  letter-spacing: 2.28px;
}


.Header__logo {
    background-image: url(/static/media/BY_OUTgnFUs.b0af84ac.jpg);
    background-size: 70px;
    background-position: center;
    background-repeat: no-repeat;
    margin: 0;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    box-shadow: 0 0 6px 5px white;
}

.Header__logo:hover {
    box-shadow: 0 0 6px 5px white inset;
    transition: all .4s;
}

.navbar-dark .navbar-nav .nav-link {
    opacity: 1;
    text-transform: uppercase;
    color: #ffffff;
    align-self: center;
    padding: 15px;
    border-radius: 30%;
}
.navbar-dark .navbar-nav .nav-link:hover {
    color: #ffffff;
    box-shadow: 0 0 6px 5px white inset;
}
.navbar-toggler{
    margin-right: 20px;
}
.navbar, .Header__container{
    background-color: black;
    box-shadow: 0 0 10px 1px white;
    padding: 10px 40px;
}
.navbar-toggler {
    margin: 0;
}

 .Welcome {
     margin-top: 90px;
     display: grid;
     background-image: url("/static/media/here's the light.7b639920.jpg");
     background-color: #000000;
     background-size: cover;
     background-position: center;
     background-repeat: no-repeat;
     height: 80vw;
     min-height: 375px;
 }
 .Welcome .container {
     align-self: end;
 }
 .Welcome__logo-container {
     height: 15vw;
     display: flex;
     flex-direction: row;
     justify-content: space-around;
 }
 .Welcome__logo-container a {
     position: relative;
     width: 6vw;
     height: 6vw;
     border-radius: 50%;
     border: 3px white;
 }
 .Welcome__logo {
     top: 0;
     left: 0;
     position: absolute;
     width: 6vw;
     height: 6vw;
     border-radius: 50%;
 }
 .Welcome__logo:hover {
     border: 7vw white;
     top: -5px;
     left:-5px;
     width: 7vw;
     height: 7vw;
     box-shadow: 0 0 20px 5px white , 0 0 20px 5px white inset;
     transition: all .4s;
 }
 .Welcome__logo:active {
     border: 7vw white;
     top: -5px;
     left:-5px;
     width: 7vw;
     height: 7vw;
     box-shadow: 0 0 20px 5px white , 0 0 20px 5px white inset;
     transition: all .4s;
 }




.card {
    margin: 20px;
    padding-bottom: 20px;
    box-sizing: padding-box;
    text-align: start;
    box-shadow: 0 2px 9px 1px rgba(0, 0, 0, 0.16), 0 4px 6px 1px rgba(0, 0, 0, 0.24);
    cursor: pointer;
    min-width: 300px;
    min-height: 300px;
    max-width: 300px;
    max-height: 300px;
}

.News__item:hover {
    box-shadow: 0 4px 19px 1px rgba(0, 0, 0, 0.16), 0 9px 13px 1px rgba(0, 0, 0, 0.24);
}
.News__wrap {
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.News__item .cart-body {
    overflow: hidden;
    text-overflow: ellipsis;
}

.News__item .card-title {
    opacity: 0.96;
    color: #000000;
    font-size: 24px;
    font-weight: 500;
    line-height: 28px;
}
.News__item .card-text {
    font-size: 14px;
    font-weight: 400;
    box-sizing: border-box;
}

.News__title {
    color: #000000;
    font-family: Roboto;
    font-size: 60px;
    font-weight: 400;
    line-height: 68px;
    margin: 80px 0 64px 0;
}

.News__show-more-btn {
    margin-top: 2vw;
}

.Button {
     width: 130px;
     height: 40px;
     box-shadow: 0 10px 12px rgba(21, 0, 50, 0.6);
     border-radius: 20px;
     background-color: #6200ea;
     color: #ffffff;
     font-family: Roboto;
     font-size: 14px;
     font-weight: 400;
     line-height: 24px;
     text-transform: uppercase;
 }

.Button:hover {
     cursor: pointer;
     width: 130px;
     height: 40px;
     border-radius: 20px;
     color: #ffffff;
     font-family: Roboto;
     font-size: 14px;
     font-weight: 400;
     line-height: 24px;
     text-transform: uppercase;
     box-shadow: 0 2px 10px 1px rgba(0, 0, 0, 0.16), 0 8px 11px 1px rgba(0, 0, 0, 0.24);
     background-color: #7213f6;
 }
.Button:active {
    cursor: pointer;
    width: 130px;
    height: 40px;
    box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.16), 0 2px 2px 1px rgba(0, 0, 0, 0.24);
    border-radius: 20px;
    background-color: #5900d4;
    color: #ffffff;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
    text-transform: uppercase;
}
.AboutUs {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.AboutUs__slider-wrap {
    padding: 0;
}

.AboutUs__text {
    background-color: #ebebeb;
    padding: 6vw 8vw;
    box-sizing: border-box;
}

.AboutUs__text h1 {
    text-align: center;
}

.AboutUs__text p {
    font-weight: 400;
    text-align: start;
    padding-top: 20px;
    font-size: 16px;
    line-height: 32px;
}

.carousel, .carousel-inner, .carousel-item , .carousel-item img{
    width: 100%;
    height: 100%;
    min-height: 375px;
}



.Video {
    padding-bottom: 100px;
}
.Video__item-wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 0 10%;
}
.Video h1 {
    opacity: 0.96;
    color: #000000;
    font-family: Roboto;
    font-size: 60px;
    font-weight: 400;
    line-height: 68px;
    margin-bottom: 120px;
    margin-top: 100px;
}
.Item {
    box-shadow: 0 2px 9px 1px rgba(0, 0, 0, 0.16), 0 4px 6px 1px rgba(0, 0, 0, 0.24);
}
.Item:hover {
    box-shadow: 0 4px 19px 1px rgba(0, 0, 0, 0.16), 0 9px 13px 1px rgba(0, 0, 0, 0.24);
}
.PhotoItem {
  cursor: pointer;
  max-height: 200px;
}
.PhotoItem:focus,
.PhotoItem:active,
.Photo__wrap button:focus,
.Photo__wrap button:active {
  outline: none;
}

@media only screen  and (min-device-width: 992px) and (max-device-width: 1199px){
    .Photo__wrap button:nth-last-child(-n+2) {
        display: none;
    }
}
@media only screen  and (min-device-width: 1200px)and (max-device-width: 2000px){
    .Photo__wrap button:nth-last-child(-n+3) {
        display: none;
    }
}
@media only screen  and (min-device-width: 0)and (max-device-width: 429px){
    .Photo__wrap button:nth-last-child(-n+6) {
        display: none;
    }
}

.Photo {
    padding-top: 6vw;
    padding-bottom: 6vw;
}
.Photo__wrap {
    padding-top: 6vw;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
}

.Photo__wrap button {
    padding: 0;
    border: none;
}

.Shop__items-wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}


.ShopItem {
    box-shadow: 0 2px 9px 1px rgba(0, 0, 0, 0.16), 0 4px 6px 1px rgba(0, 0, 0, 0.24);
    margin-top: 20px;
}
.ShopItem:hover {
    cursor: pointer;
    box-shadow: 0 4px 19px 1px rgba(0, 0, 0, 0.16), 0 9px 13px 1px rgba(0, 0, 0, 0.24);
}
.ShopItem__name {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    text-align: start;
}
.ShopItem__text-wrap {
    padding: 16px;
}
.ShopItem__price {
    text-align: start;
    font-size: 18px;
}

.Concerts {
  height: 720px;
  padding: 100px 11.7%;
  display: flex;
  flex-direction: row;
}
.Concerts__image,
.Concerts__content {
  width: 50%;
  height: 100%;
}

.Concerts__image {
  background-image: url(data:image/png;base64,),
    url(/static/media/concerts-bg2.867fa491.png);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.Concerts__image span {
  color: #ffffff;
  font-family: Roboto;
  font-size: 60px;
  font-weight: 400;
  line-height: 73.79px;
}

.Concerts__content {
    width: 50%;
}

.ConcertItem {
    display: flex;
    cursor: pointer;
    flex-direction: row;
    padding: 20px 30px;
    justify-content: space-between;
    box-shadow: 0px 0px 2px rgba(0,0,0,0.5);
}
.ConcertItem:hover {
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
}
.ConcertItem:nth-child(2n){
    background-color: #ebebeb;
}
.ConcertItem__icon img {
    width: 38px;
    height: 28px;
    align-self: start;
}
.ConcertItem__date {
    text-align: left;
    padding-left: 32px;
    align-self: center;
}
.ConcertItem__country {
    width: 160px;
    text-align: center;
    align-self: center;
}
.ConcertItem__club {
    align-self: center;
    text-align: right;
    color: #6200ea;
    font-family: Roboto;
    font-size: 16px;
    font-weight: 400;
}
.ConcertItem__date, .ConcertItem__country {
    opacity: 0.96;
    color: #000000;
    font-family: Roboto;
    font-size: 16px;
    font-weight: 500;
}
.Footer {

    height: 123px;
    background-color: #212121;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 0 11.7%;
}
.Footer__coop, .Footer__social, .Footer__dev-info {
    width: 300px;
    height: 48px;
    align-self: center;
}
.Footer__coop-key {
    opacity: 0.48;
    color: #ffffff;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 400;
    align-self: start;
    text-align: left;
    min-width: 100%;
    min-height: 50%;
}
.Footer__coop-value {
    opacity: 0.96;
    color: #ffffff;
    font-family: Roboto;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
    min-width: 100%;
    min-height: 50%;
}
.Footer__social-wrap {
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.Footer__social-wrap a {
    margin: 5px;
    cursor: pointer;
}
.Footer__social-title {
    opacity: 0.48;
    color: #ffffff;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
}
.Footer__dev-info {
    display: flex;
    flex-direction: column;
}
.Footer__dev-info-r {
    opacity: 0.96;
    color: #ffffff;
    font-family: Roboto;
    font-size: 18px;
    font-weight: 400;
    line-height: 33.79px;
}
.Footer__dev-info-d {
    opacity: 0.48;
    color: #ffffff;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
}

